.users-block {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.users-block button {
  font-size: 15px;
  color: red;
  margin-top: 15px;
  width: 225px;
  padding: 15px 0px;
  border-radius: 5px;
  background-color: rgba(255, 0, 0, 0.2);
  border: 2px solid rgba(255, 0, 0, 0.4);
}

.users-block button:hover {
  background-color: rgba(255, 0, 0, 0.4);
}
.users-block button:active {
  background-color: rgba(255, 0, 0, 0.2);
}

.users-block button:disabled {
  background-color: rgba(255, 0, 0, 0.1);
  border: 2px solid rgba(255, 0, 0, 0.2);
}
