.block {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  width: calc(100vw - 250px);
  height: 150px;
  box-sizing: border-box;
  font-size: 50px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.block h2 {
  margin: 15px;
  font-size: 45px;
}

.block span {
  font-size: 15px;
  color: gray;
}

.block button {
  font-size: 15px;
  color: red;
  margin-top: 15px;
  width: 225px;
  padding: 15px 0px;
  border-radius: 5px;
  background-color: rgba(255, 0, 0, 0.2);
  border: 2px solid rgba(255, 0, 0, 0.4);
}

.block button:hover {
  background-color: rgba(255, 0, 0, 0.4);
}
.block button:active {
  background-color: rgba(255, 0, 0, 0.2);
}
.block button:disabled {
  background-color: rgba(255, 0, 0, 0.1);
  border: 2px solid rgba(255, 0, 0, 0.2);
}

.movie-block {
  margin-top: 35px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.movie-block img {
  width: 250px;
}

.movie-block p {
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 0;
}
.movie-block span {
  font-size: 20px;
  font-weight: 600;
  color: gray;
}
