.user {
  height: 250px;
  width: 250px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.user img {
  width: 150px;
  height: 150px;
  margin: 0 auto;
}

.user input {
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  /* color: rgba(0, 0, 0, 0.8); */
  outline: 2px solid rgba(128, 128, 128, 0.4);
  border: none;
  padding: 10px;
}

.user input:focus {
  outline: 2px solid aqua;
}
